      <h1 class="fw-700 text-xl">Mis Bitácoras</h1>
      <h1 class="fw-600 text-xs">Tienes (7) parámetos asignados</h1>
      <div class="flex flex-row items-center justify-center py-2">
        <div
          class="w-2 h-2 rounded-full"
          :class="[conexion ? 'bg-green-500' : 'bg-red-500']"
        ></div>
        <h1
          class="ml-1 text-xxs uppercase fw-800"
          :class="[conexion ? 'text-green-500' : 'text-red-500']"
        >
          {{ conexion ? "ONLINE" : "OFFLINE" }}
        </h1>
      </div>
      <!-- FILTROS DE LAS BITACORAS -->
      <div class="w-full py-3">
        <div
          class="
            bg-white
            rounded-t-lg
            py-4
            px-4
            flex flex-col
            md:flex-row
            md:items-end
            items-center
            justify-center
            md:justify-center
            flex-wrap
            mb-4
            md:mb-0
          "
        >
          <div class="md:mr-4 mx-0 w-11/12 md:w-auto mb-3 md:mb-0">
            <h1 class="font-bold text-xxs uppercase text-left">
              Tipo de activo:
            </h1>
            <select
              name="por"
              class="
                bg-gris-30
                trans
                rounded-md
                px-3
                h-8
                focus:outline-none
                w-full
                md:w-auto
              "
            >
              <option value="Todos">Todos</option>
              <option value="Calderas">Calderas</option>
              <option value="UMAS">UMAS</option>
              <option value="UTAS">UTAS</option>
              <option value="Extractores">Extractores</option>
              <option value="Inyectores">Inyectores</option>
            </select>
          </div>

          <div
            class="
              relative
              flex
              items-center
              w-full
              md:w-auto
              px-10
              md:px-0
              mb-2
              md:mb-0
              mr-0
              md:mr-4
            "
          >
            <input
              type="seach"
              class="
                bg-gris-30
                trans
                w-full
                md:w-auto
                rounded-l-md
                pl-6
                h-8
                focus:outline-none
              "
              placeholder="Buscar..."
            />
            <svg
              xmlns="http://www.w3.org/2000/svg"
              class="w-4 h-4 absolute top-0 mt-2 ml-1 text-gris-600"
              viewBox="0 0 24 24"
              fill="none"
              stroke="currentColor"
              stroke-width="3"
              stroke-linecap="round"
              stroke-linejoin="round"
            >
              <circle cx="11" cy="11" r="8"></circle>
              <line x1="21" y1="21" x2="16.65" y2="16.65"></line>
            </svg>
            <button
              class="
                focus:outline-none
                bg-bluedemon-200
                rounded-r-md
                h-8
                w-8
                flex
                items-center
                justify-center
              "
            >
              <svg
                xmlns="http://www.w3.org/2000/svg"
                class="w-4 h-4 text-bluedemon-400"
                viewBox="0 0 24 24"
                fill="none"
                stroke="currentColor"
                stroke-width="3"
                stroke-linecap="round"
                stroke-linejoin="round"
              >
                <circle cx="11" cy="11" r="8"></circle>
                <line x1="21" y1="21" x2="16.65" y2="16.65"></line>
              </svg>
            </button>
          </div>
          <div class="mx-0 w-10/12 md:w-auto">
            <button
              class="
                bg-yellow-200
                text-yellow-500
                h-8
                rounded-lg
                px-2
                text-xs
                w-10/12
                md:w-auto
                fw-500
                uppercase
              "
              v-if="conexion"
            >
              Historico
            </button>
          </div>
        </div>
      </div>
    </div>
    <!-- Fila de columnas -->
    <div class="contbotones">
      <div class="seccion">
        <div class="py-2">
          <h1 class="font-semibold">
            PARA HOY
            <span class="font-bold">({{ arrayParametros.length }})</span>
          </h1>
        </div>

        <Entradabit
          v-for="(x, y) in arrayParametros"
          :key="y"
          :index="y"
          :array="x"
        />
      </div>
      <div class="seccion">
        <div class="py-2">
          <h1>Pendientes <span class="font-bold text-xs">(5)</span></h1>
        </div>
        <Entradabit
          v-for="(x, y) in arrayParametros"
          :key="y"
          :index="y"
          :array="x"
        />
      </div>
      <div class="seccion">
        <div class="py-2">
          <h1>Llenos <span class="font-bold text-xs">(1)</span></h1>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { computed, onMounted } from "@vue/runtime-core";
import Entradabit from "../components/Entradabit.vue";
import { useStore } from "vuex";
import { ref } from "@vue/reactivity";

export default {
  components: { Entradabit },
  setup() {
    const store = useStore();

    const conexion = ref(true);

    onMounted(() => {
      const status = async () => {
        setInterval(async () => {
          try {
            const response = await store.dispatch("getConexion", {
              idDestino: localStorage.getItem("idDestino"),
              idUsuario: localStorage.getItem("usuario"),
              action: "conexion",
            });

            if (response) {
              conexion.value = true;
              getArray();
            } else {
              conexion.value = false;
              window.stop();
            }
          } catch (error) {
            conexion.value = false;
            window.stop();
          }
        }, 1000);
      };
      status();
    });

    const getArray = () => {
      store.dispatch("getArray", {
        idDestino: localStorage.getItem("idDestino"),
        idUsuario: localStorage.getItem("usuario"),
        action: "getArray",
      });
    };

    const arrayParametros = computed(() => {
      return store.state.arrayParametros;
    });

    return { conexion, arrayParametros };
  },
};
</script>

<style scoped>
.contbotones {
  scroll-snap-type: x mandatory;
  overflow-x: scroll;
  scrollbar-width: none; /* Firefox */
  -ms-overflow-style: none; /* Internet Explorer 10+ */
  @apply w-full flex flex-row justify-center items-start px-2;
}

.contbotones::-webkit-scrollbar {
  /* WebKit */
  width: 0;
  height: 0;
}
.seccion {
  scroll-snap-align: center;
  flex: none;
  width: 100%;
}

@media (min-width: 768px) {
  .seccion {
    width: 33.333333%;
  }
}
</style>
