<template>
  <div
    @click="abierto = !abierto"
    class="
      cursor-pointer
      p-2
      text-left
      fw-600
      mb-2
      flex flex-row
      items-center
      justify-between
    "
  >
    <h1>
      <span>{{ index + 1 }} .- </span>{{ array.parametro }}
    </h1>
    <svg
      :class="{ girar: abierto }"
      xmlns="http://www.w3.org/2000/svg"
      width="24"
      height="24"
      viewBox="0 0 24 24"
      fill="none"
      stroke="currentColor"
      stroke-width="2"
      stroke-linecap="round"
      stroke-linejoin="round"
    >
      <path d="M6 9l6 6 6-6" />
    </svg>
  </div>

  <div v-if="abierto" class="bg-gris-30 px-2 py-4">
    <Filaentrada v-for="(x, y) in array.equipos" :key="y" :equipo="x" />
  </div>
</template>

<script>
import { ref } from "@vue/reactivity";
import Filaentrada from "./Filaentrada.vue";
export default {
  components: { Filaentrada },
  props: {
    array: {
      type: Object,
      required: false,
    },

    index: {
      type: Number,
      required: false,
      value: 0,
    },
  },
  setup(props) {
    const abierto = ref(false);

    return { abierto };
  },
};
</script>

<style scoped>
.girar {
  transform: rotate(180deg);
}
</style>
