import { createStore } from 'vuex'

export default createStore({
  state: {
    arrayParametros: [],

  },

  mutations: {

    setArrayParametros(state, payload) {
      state.arrayParametros = payload;
    },
  },

  actions: {

    // OBTIENE DATOS DEL ARRAY
    async getArray({ commit }, array) {
      try {
        const res = await fetch(`../apis/bitacoras_parametros.php`, {
          method: "POST",
          body: JSON.stringify(array),
        });
        const data = await res.json();
        if (data.response == "SUCCESS") {
          console.log(data.data)
          commit("setArrayParametros", data.data);
          return true;
        }

        return false;
      } catch (error) {
        console.log(error);
        return false;
      }
    },

    // COMPRUEBA LA CONEXIÓN
    async getConexion({ commit }, array) {
      try {
        const res = await fetch(`../apis/bitacoras_parametros.php`, {
          method: "POST",
          body: JSON.stringify(array),
        });
        const data = await res.json();
        if (data.response == "SUCCESS") {
          return await data.conexion;
        }
        return false;
      } catch (error) {
        console.log(error);
        return false;
      }
    },

  },

  modules: {
  }
})
